import React from "react"
import { Link } from "gatsby"

const Pagination = ({ numPages, currentPage, contextPage }) => {
  if (numPages <= 1) {
    return null
  }

  return (
    <ul className="flex pagination mt-8 mb-0">
      {Array.from({ length: numPages }).map((item, i) => {
        const index = i + 1

        const baseLink = `/blog/${contextPage ? `${contextPage}/` : ""}`
        const link = index === 1 ? baseLink : `${baseLink}page/${index}`

        return (
          <li className={currentPage === index ? "current" : "btn"} key={link}>
            {currentPage === index ? (
              <span>{index}</span>
            ) : (
              <Link to={link}>{index}</Link>
            )}
          </li>
        )
      })}
    </ul>
  )
}

export default Pagination
